.login-view.wrapper {
  width: 500px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.login-view.box,
.login-view form {
  display: flex;
}

.login-view.box {
  width: 100%;
  height: 80px;
  background-color: white;
  border-radius: 16px;
  align-items: center;
  box-shadow: 0px 0px 26px 5px rgba(0, 0, 0, 0.73);
}

.login-view.box,
.login-view.box form {
  display: flex;
}

.login-view.box form {
  width: 100%;
  align-items: center;
  gap: 10px;
}

.login-view.box input {
  border: none;
  height: 30px;
  color: black;
  border-radius: 8px;
}

.login-view.box input:focus {
  outline: none;
}

.login-view.box input[type="submit"] {
  width: 30%;
  margin-right: 30px;
  cursor: pointer;
  border: 2px solid #f62c72;
  background: transparent;
  transition: background 0.2s ease-in;
}

.login-view.box input[type="submit"]:hover {
  background-color: #f62c72;
}

.login-view.box input[type="text"] {
  background: transparent;
  border: 2px solid #f62c72;
  padding-left: 10px;
  width: 100%;
  color: black;
  margin-left: 30px;
}

.login-view.box input[type="text"]::placeholder {
  color: black;
}

.login-view.errors {
  width: 100%;
  border-radius: 16px;
  height: 20px;
  color: red;
  font-size: 15px;
  align-items: center;
  justify-content: center;
  background-color: white;
  box-shadow: 0px 0px 7px 5px rgba(0, 0, 0, 0.28);
}

@media only screen and (max-width: 700px) {
  .login-view.wrapper {
    width: 350px;
  }

  .login-view.box input {
    height: 40px;
  }
}

@media only screen and (max-width: 375px) {
  .login-view.wrapper {
    width: 300px;
  }

  .login-view.box input {
    height: 40px;
  }
}
