.new-product.body {
  width: 30%;
  border-radius: 5px;
  display: flex;
  flex-wrap: wrap;
  background-color: #ecf0f1;
}

.new-product.title {
  text-align: center;
  width: 100%;
  margin: 10px 0 10px 0;
  font-size: 25px;
}

.new-product.fields {
  padding: 0 40px 40px 40px;
}

@media only screen and (max-width: 875px) {
  .new-product.body {
    width: 90%;
  }

  .new-product.fields {
    padding: 0 30px 30px 30px;
  }
}
