@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.login-view.wrapper {
  width: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
}

.login-view.box,
.login-view form {
  display: -webkit-flex;
  display: flex;
}

.login-view.box {
  width: 100%;
  height: 80px;
  background-color: white;
  border-radius: 16px;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 0px 26px 5px rgba(0, 0, 0, 0.73);
}

.login-view.box,
.login-view.box form {
  display: -webkit-flex;
  display: flex;
}

.login-view.box form {
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.login-view.box input {
  border: none;
  height: 30px;
  color: black;
  border-radius: 8px;
}

.login-view.box input:focus {
  outline: none;
}

.login-view.box input[type="submit"] {
  width: 30%;
  margin-right: 30px;
  cursor: pointer;
  border: 2px solid #f62c72;
  background: transparent;
  transition: background 0.2s ease-in;
}

.login-view.box input[type="submit"]:hover {
  background-color: #f62c72;
}

.login-view.box input[type="text"] {
  background: transparent;
  border: 2px solid #f62c72;
  padding-left: 10px;
  width: 100%;
  color: black;
  margin-left: 30px;
}

.login-view.box input[type="text"]::-webkit-input-placeholder {
  color: black;
}

.login-view.box input[type="text"]:-ms-input-placeholder {
  color: black;
}

.login-view.box input[type="text"]::placeholder {
  color: black;
}

.login-view.errors {
  width: 100%;
  border-radius: 16px;
  height: 20px;
  color: red;
  font-size: 15px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: white;
  box-shadow: 0px 0px 7px 5px rgba(0, 0, 0, 0.28);
}

@media only screen and (max-width: 700px) {
  .login-view.wrapper {
    width: 350px;
  }

  .login-view.box input {
    height: 40px;
  }
}

@media only screen and (max-width: 375px) {
  .login-view.wrapper {
    width: 300px;
  }

  .login-view.box input {
    height: 40px;
  }
}

.handle-product.form {
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.handle-product:not(.handle-product.error) {
  display: -webkit-flex;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}

.handle-product.body,
.handle-product.field,
.handle-product.field * {
  width: 100%;
}

.handle-product.field *:not(button[type="submit"]) {
  border: none;
  outline: none;
  border-radius: 6px;
  border: 2px solid #dfe6e9;
  padding-left: 8px;
  font-size: 20px;
  /* background-color: white; */
  /* background-color: red; */
  backgroud: #f00;
}

.handle-product.field *::-webkit-input-placeholder {
  color: #7f8c8d;
}

.handle-product.field *:-ms-input-placeholder {
  color: #7f8c8d;
}

.handle-product.field *::placeholder,
.handle-product.field select,
.handle-product.field input[type="submit"] {
  color: #7f8c8d;
}

.handle-product.field textarea {
  resize: none;
  height: 150px;
}

.handle-product.error.active {
  color: white;
  font-size: 12px;
  width: 100%;
  color: #7f8c8d;
}

.handle-product.error.disabled {
  display: none;
}

.new-product.body {
  width: 30%;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  background-color: #ecf0f1;
}

.new-product.title {
  text-align: center;
  width: 100%;
  margin: 10px 0 10px 0;
  font-size: 25px;
}

.new-product.fields {
  padding: 0 40px 40px 40px;
}

@media only screen and (max-width: 875px) {
  .new-product.body {
    width: 90%;
  }

  .new-product.fields {
    padding: 0 30px 30px 30px;
  }
}

body {
  margin: 0;
  background-color: rgba(26, 26, 29, 1);
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

* {
  box-sizing: border-box;
}

